#timeContainer {
    display: none;
    @media (min-width: 732px) {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: right;
        flex: 1 0 0;
        white-space: nowrap;
    }
}

#recentlyVisitedSites {
    display: flex;
    visibility: hidden;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    flex: 1 0 0;
    margin-top: 36px;
    @media (min-height: 635px) {
        visibility: visible;
    }
    max-width: calc(100% - 56px);
}

#brandLogoContainer {
    display: none;
    width: 56px;
    height: 56px;
    position: absolute;
    bottom: 56px;
    right: 56px;
    @media (min-width: 732px) {
        display: flex;
    }
}
